import {useTdlib} from './components/tdlib)/Context'
import styled from 'styled-components'

const Container = styled.div`
  margin: auto;
  max-width: 80%;
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
`
const Div = styled.div`
  width: 100%;
  background-color: white;
  height: 20px;
`

function MainApp(){
    return(
        <Container>
            <Div></Div>
        </Container>
    )
}
export default MainApp;