import styled from 'styled-components'
import {load3} from './components/css/animations'

const Preloader = styled.div`
  &:after {
  background: rgb(27, 27, 27);
  width: 90%;
  height: 90%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  }
  overflow:hidden;
  font-size: 10px;
  margin: 42vh auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: -moz-linear-gradient(#FF7B92, 10%, rgba(101,171,248, 0) 42%);
  background: -webkit-linear-gradient(#FF7B92, 10%, rgba(101,171,248, 0) 42%);
  background: -o-linear-gradient(#FF7B92, 10%, rgba(101,171,248, 0) 42%);
  background: -ms-linear-gradient(#FF7B92, 10%, rgba(101,171,248, 0) 42%);
  background: linear-gradient(to right, #FF7B92 10%, rgba(101,171,248, 0) 52%);
  -webkit-animation: ${load3} 1.3s infinite linear;
  animation: ${load3} 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
`

function Loading(){
    return(
        <Preloader/>
    )
}

export default Loading;